.mySwiper {
}

.swiper {
  height: 100%;
}
.swiper-button-prev,
.swiper-button-next {
  color: #656565;
}


.swiper-slide {
  width: 100%;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
}

.about-grid-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-auto-rows: auto;
  width: 80%;
  justify-items: center;
}
.icon-img {
  height: 6.5vh;
  width: auto;
  background-color: white;
  padding:8px;
  margin: 3px -10px;
  border-radius: 10px;
  border: 1px solid #c1c1c1; 
}

@media screen and (max-width: 800px) {
  .about-grid-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-auto-rows: auto;
  justify-content: space-evenly;
  justify-items: center;
  align-content: space-evenly;
  align-items: center;
}

.icon-img {
  height: 6vh;
  width: auto;
  background-color: none;
  padding: 6%;

  margin-top: 10%;
  border-radius: 10px;
  border: 1px solid #d7d7d7; 
}
}